import '@styles/styles.css';
import { gsap } from "gsap-trial";
import { ScrollTrigger } from "gsap-trial/ScrollTrigger.js";
import { ScrollSmoother } from "gsap-trial/ScrollSmoother.js";
gsap.registerPlugin(ScrollTrigger, ScrollSmoother);


if (ScrollTrigger.isTouch !== 1) {
  ScrollSmoother.create({
    wrapper: '.smooth-wrapper',
    content: '.smooth-content',
    smooth: 1.5,
    effects: true,
  });

  gsap.fromTo('.hero-section',
    { opacity: 1, },
    {
      opacity: 0,
      scrollTrigger: {
        trigger: '.hero-section',
        start: 'center',
        end: '850',
        scrub: true,
      },
    },
  );

  const itemsLeft = gsap.utils.toArray('.gallery-column .gallery-column--left gall.gallery__item');

  itemsLeft.forEach((item) => {
    gsap.fromTo(item,
      {
        x: -500,
        opacity: 0,
      },
      {
        opacity: 1,
        x: 0,
        scrollTrigger: {
          trigger: item,
          scrub: true,
        }
      },
    );
  });

  const itemsRight = gsap.utils.toArray('.gallery-column .gallery-column--right gall.gallery__item');

  itemsRight.forEach((item) => {
    gsap.fromTo(item,
      {
        x: 500,
        opacity: 0,
      },
      {
        opacity: 1,
        x: 0,
        scrollTrigger: {
          trigger: item,
          scrub: true,
        }
      },
    );
  });
};